import React from "react";
import { BrowserRouter, Route, Link, Switch } from "react-router-dom";

import App from "../App";
import Header from "../components/Header";

export default () => (
    <BrowserRouter>
        <Switch>
            <Route exact path ="/" component={App}/>
            <Route exact path ="/header" component={Header}/>
        </Switch>
    </BrowserRouter>
)